.App {
  text-align: center;
  }
  
  .container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  }
  
  .question {
  font-size: 20px;
  margin-bottom: 10px;
  text-align: left;
  }
  
  .options {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 20px;
  }
  
  .option {
  margin-bottom: 10px;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  cursor: pointer;
  width: 100%;
  border-radius: 8px;
  transition: background-color 0.3s; /* Adiciona uma transição suave */
  user-select: none;
  }
  
  .option:hover {
  background-color: rgba(0, 0, 0, 0.1);
  }
  
  .option:active {
  background-color: rgba(0, 0, 0, 0.2);
  }
  
  .option.selected {
  background-color: rgba(0, 0, 0, 0.1); /* Adiciona um estilo para a opção selecionada */
  }
  
  .result {
  font-size: 24px;
  font-weight: bold;
  margin-top: 20px;
  text-align: left;
  }